/* Tablet  */
@media screen and (max-width: 788px) {
  .contact-box {
    grid-template-columns: 1fr 1fr;
  }
  .left {
    height: 100%;
  }
  .contact-container {
    position: relative;
    left: 100px;
    width: 85vw;
  }
}
/* THIS IS TO MAKE FORM MOBILE FRIENDLY WHICH DOES WORK! */
/* Iphone responsive */
@media screen and (max-width: 414px) {
  .contact-box {
    grid-template-columns: 1fr;
  }

  .left {
    height: 200px;
  }

  .contact-container {
    position: relative;
    padding-top: 10vh;
    padding-bottom: 20vh;
    width: 100vw;
    left: 0vw;
  }
}
